@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
 
  --gradient-text: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
  --gradient-bar: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
  --gradient-group-linkedin: linear-gradient(90deg, rgba(51, 120, 204, 1) 0%, rgba(22, 38, 77, 1) 100%);
  --gradient-group-portfolio: linear-gradient(90deg, rgba(143, 38, 147, 1) 0%, rgba(218, 52, 52, 1) 100%);
  --gradient-group-instagram: linear-gradient( 45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100% );
  --gradient-group-github: linear-gradient(90deg, rgba(39, 39, 40, 1) 0%, rgba(0, 0, 0, 1) 100%);
  
  
  --color-bg: linear-gradient(90deg, rgba(154, 25, 69, 1) 0%, rgba(103, 58, 183, 1) 100%);
  --color-text: #81AFDD;
  --color-subtext: #03A9F4;
}

.gradient__text {
  background: var(--gradient-text);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-background-clop: text;
  -webkit-text-fill-color: transparent;
}


body {
  padding: 0;
  margin: 0;
  background: var(--color-bg);
 
}

body::-webkit-scrollbar {
  width:10px;
  height: 10px;
}

body::-webkit-scrollbar-track {
  background:#333;
}

body::-webkit-scrollbar-thumb {
  background: #8303b6;
  border-radius: 4px;
}


.container img {
  margin: 20px;
  padding: 20px;
  height: 30%;
  width: 30%;
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 0.1rem;
  text-align: center;
  align-items: center;
}

.container :hover{
  transform: scale(1.02);
  transition: all 0.1s ease-in-out;
  border:#FFF;  
}

.text {
  font-size: 25px;
  text-align: center;
  align-items: center;
  margin-top: 90px;
  color: var(--color-text);
  cursor: pointer;
}

.container {
  display: flex;
  justify-items: center;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 0 auto;
  grid-gap: 0.5rem;
  gap: 0.5rem; 
  padding: 5px;
  margin-bottom: 0.5rem;
  flex-grow: 1;
}


.social {
  margin: 20px;
  padding: 30px;
  height: 80px;
  width: 50vh;
  text-align: center;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: white;
  text-transform: none;
}

a:visited {
  text-decoration: none;
}

#linkedin {
  background: var(--gradient-group-linkedin);
  text-align: center;
  border-radius: 18px;  
 
  
}

#portfolio {
  background: var(--gradient-group-portfolio);
  text-align: center;
  border-radius: 18px;
}

#instagram {
  top: 1px;
  background: var(--gradient-group-instagram);
  text-align: center;
  border-radius: 18px;
}

#github {
  background: var(--gradient-group-github);
  text-align: center;
  border-radius: 18px;
}

 #icon {
  margin-bottom: -3px;
  color: var(--color-subtext);
}

@media screen and (max-width: 365px){
    .container img {
      width: 100%;
      height: 100%;
      margin-left: -20px;
      
    }

    .text {
      text-align: center;
      align-items: center;
      margin-left: 85px;
    }
}
